import React from 'react';
import {connect} from 'react-redux'
import { fetchTitles } from '../actions';
import { Link } from 'react-router-dom';

class TitleList extends React.Component {
    componentDidMount() {
        this.props.fetchTitles();
    };

    helperUrl = id => {
        return `/articles/${id}`;
    }

    renderList() {
        return this.props.titles.map(title => {
            return (
                <div className="item">
                    <i className="large angle double right icon"></i>
                    <div className="content">
                        <Link to={`/articles/${title.id}`} className="header">
                            {title.title}
                        </Link>
                        <div className="description">{title.created_at}</div>
                    </div>
                </div>
            );
        })
    }

    render() {
        return (
            <div className="ui relaxed divided list">
                {this.renderList()}
            </div>
        );
    }
};

const mapStateToProps = (state) => {
    return { titles: state.titles };
}

export default connect(mapStateToProps, { fetchTitles })(TitleList);