import blogApi from '../apis/blogApi';

export const fetchPosts = (query) => async dispatch => {
    const response = await blogApi.get(`/articles/${query || ''}`);

    dispatch({ type: 'FETCH_POSTS', payload: response.data })
};


export const fetchTitles = () => async dispatch => {
    const response = await blogApi.get('/titles/');

    dispatch({ type: 'FETCH_TITLES', payload: response.data })
};

export const fetchPost = id => async dispatch => {
    const response = await blogApi.get(`/articles/${id}/`);

    dispatch({ type: 'FETCH_POST', payload: response.data })
};