import './PostList.css';
import React from 'react';
import {connect} from 'react-redux';
import { fetchPosts } from '../actions';
import marked from 'marked';
import hljs from 'highlightjs';

import CommentBox from './CommentBox';

class PostList extends React.Component {
    componentDidMount() {
        this.props.fetchPosts();
        marked.setOptions({
            highlight: function(code, lang) {
              return hljs.highlightAuto(code, [lang]).value;
            }
        });
    }

    buttonPressNext = (event) => {
        if (this.props.posts.next) {
            const targeturl = this.props.posts.next;
            const query = targeturl.match(/\?page=(.*?)(&|$)/);
            this.props.fetchPosts(query[0]);
            window.scrollTo(0,0)
        }
    }

    checkNext = () => {
        if (!(this.props.posts.next)) {
            return {opacity: 0.3, cursor:'default'}
        }
    }

    renderList() {
        if (this.props.posts.results === undefined) {
            return null;
        }
        return this.props.posts.results.map(post => {
            return (
                <div className="ui segment">
                    <div className="item" key={post.id}>
                        <div className="content">
                            <div className="description">
                                <h1 className="ui dividing header">{post.title}</h1>
                                <div dangerouslySetInnerHTML={{__html: marked(post.body)}}></div>
                                <CommentBox articleId={post.id} />
                            </div>
                        </div>
                    </div>
                </div>
            );
        })
    }

    render() {
        console.log(this.props)
        console.log(this.props.posts.next)
        return (
            <div className="ui relaxed divided list">
                {this.renderList()}
                <div className="ui segment">
                    <div className="fluid ui olive labeled icon button" onClick={this.buttonPressNext} style={this.checkNext()} >
                        <i className="icon chevron circle right"></i> Next Page
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return { posts: state.posts };
}

export default connect(mapStateToProps, { fetchPosts })(PostList);