import _ from 'lodash';
import React from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import blogApi from '../../apis/blogApi';

class LoginNotification extends React.Component {

    state = { func: function(){toast.info("Login required to create an article")} }

    loginCheck = () => {
        const ACCESSTOKEN = localStorage.getItem('access');
        if (!ACCESSTOKEN) {
            this.setState({func: function(){
                toast.error("You are not logged in")}}) 
        } else {
            (async () => {
                const response = await blogApi.post("/auth/jwt/verify/", {
                    token: ACCESSTOKEN
                });
                if (response.status !== 200) {
                    this.setState({func: function(){
                        toast.warn("Your token has expired")}})
                } else {
                    this.setState({func: function(){
                        toast.success("You are logged in")}})
                }
                
            })();
        }
    }

    shouldComponentUpdate(nextProps, nextState) {
        const propsDiff = _.isEqual(nextProps, this.props);
        const stateDiff = _.isEqual(nextState, this.state);
        return !(propsDiff && stateDiff);
      }

    componentDidMount(){
        this.loginCheck()
    }

    render() {
        this.addNotification = this.state.func
        return (
            <div>
                {this.addNotification()}
                <ToastContainer />
            </div>
        );
    }
}

export default LoginNotification;