import './CommentCreate.css';
import React from 'react';
import blogApi from '../apis/blogApi';

class CommentCreate extends React.Component {
    
    state = {name:'', body:'', errors:{name:'必須項目です', body:'必須項目です'}}

    handleChange = (event) => {
        event.preventDefault();
        const { name, value } = event.target;
        let errors = this.state.errors;
      
        switch (name) {
          case 'name': 
            errors.name = 
              value.length < 1
                ? '名前を入力してください！'
                : '';
            break;
          case 'body': 
            errors.body = 
              value.length >= 1 && value.length <= 200
                ? ''
                : 'コメントは1文字以上200文字以内としてください！';
            break;
          default:
            break;
        }
      
        this.setState({errors, [name]: value}, ()=> {
            console.log(errors)
        })
      }

    onFormSubmit = event => {

        blogApi.post("/comments/", {
            article: this.props.articleId,
            name: this.state.name,
            body: this.state.body,
        },
        );
        this.setState({name: '', body: ''})
        window.location.reload();
    }

    buttonPressSubmit = event => {
        event.preventDefault();
        const {errors} = this.state;
        if (!(errors.name.length > 0 || errors.body.length > 0)){
            this.onFormSubmit();  
        } 
    }

    checkSubmit = () => {
        const {errors} = this.state;
        if (errors.name.length > 0 || errors.body.length > 0){
            return {opacity: 0.3}
        } 
    }

    render() {
        
        const {errors} = this.state;

        return (
            <form className="ui reply form" onSubmit={this.onFormSubmit}>
                <div className="field">
                    <label>Name</label>
                    <input 
                        name="name"
                        type="text"
                        value={this.state.name}
                        onChange={this.handleChange}
                    />
                    {errors.name.length > 0 && 
                    <span className='error'>{errors.name}</span>}
                </div>
                <div className="field">
                    <label>Body</label>
                    <textarea
                        name="body"
                        value={this.state.body}
                        onChange={this.handleChange}
                    />
                    {errors.body.length > 0 && 
                    <span className='error'>{errors.body}</span>}
                    <div className="count">{this.state.body.length}/200</div>
                </div>
                <div className="ui primary submit labeled icon button" onClick={this.buttonPressSubmit} style={this.checkSubmit()} >
                    <i className="icon edit"></i> Add Comment
                </div>
            </form>
        )
    }

}

export default CommentCreate;