import React from 'react';
import blogApi from '../apis/blogApi';
import CommentCreate from './CommentCreate';

class CommentBox extends React.Component {

    state = { comments: [] };

    getComments = async id =>{
        const response = await blogApi.get(`/comments/?article_id=${id}`)

        this.setState({comments: response.data})
    }

    componentDidMount(){
        this.getComments(this.props.articleId)
    }
    
    renderList() {
        return this.state.comments.map(comment => {
            return (
                <div className="comment" key={comment.id}>
                    <div className="content">
                        <div className="author">{comment.name}</div>
                        <div className="text">{comment.body}</div>
                    </div>
                </div>
            );
        });
    };
    
    render() {
        return (
            <div className="ui small comments">
                <h3 className="ui dividing header">Comments</h3>
                {this.renderList()}
                <CommentCreate articleId={this.props.articleId} />
            </div>
        );
    }
}

export default CommentBox